
import Spinner from 'vux/src/components/spinner/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

import api from '../api/api'
import auth from '../api/auth'
import env from '../config/env'
export default {
    data() {
        return {
            memberShip: {},
            sex: '',
            showLoading: false,
            loadingType: 'lines'
        }
    },
    mounted() {
        this.initData()
    },
    methods: {
        async initData() {
            let vm = this
            // 显示加载动画
            vm.showLoading = true
            // 根据产品id获取用户详细信息
            api.get('getMemberById/' + localStorage.member_id).then((res) => {
                if(res.status===1){
                    // 用户对象
                    vm.memberShip = res.data
                    vm.sex = res.data.sex == '0' ? '男' : '女'
                }else{
                    vm.$vux.alert.show({
                        title: env.alertInfo,
                        content: res.message,
                        buttonText: '确定',
                        dialogTransition:'',
                        maskTransition:''
                    })
                }
                // 隐藏加载动画
                vm.showLoading = false
            }).catch(error => {
                vm.$vux.alert.show({
                    title: env.alertInfo,
                    content: env.errorInfo,
                    buttonText: '确定',
                    dialogTransition:'',
                    maskTransition:''
                })
            })
        }
    },
    components: {
        Spinner,
        XHeader, 
        Group,
        Cell
    }
}



